import React, { useRef, useEffect } from "react";
import { observer } from "mobx-react";
import { useLocalObservable } from "mobx-react-lite";
import jsQR from "jsqr";
import QrStore from "./QrStore"; // Import the store

const CameraButton = observer(() => {
    const store = useLocalObservable(() => QrStore.create({
        qrData: null,
        previousQrData: null,
        isVideoReady: false,
        facingMode: "environment",
    }));

    const videoRef = useRef(null);
    const canvasRef = useRef(null);

    useEffect(() => {
        const getCameraStream = () => {
            navigator.mediaDevices
                .getUserMedia({
                    video: { facingMode: store.facingMode },
                })
                .then((stream) => {
                    if (videoRef.current) {
                        videoRef.current.srcObject = stream;
                        videoRef.current.onloadedmetadata = () => {
                            videoRef.current.play();
                            store.setVideoReady(true);
                        };
                    }
                })
                .catch((err) => {
                    console.error("Error accessing the camera: ", err);
                    alert("Unable to access the camera. Please check your browser settings.");
                });
        };

        if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
            getCameraStream();
        } else {
            alert("Your browser does not support camera access.");
        }
    }, [store.facingMode]);

    useEffect(() => {
        const scanQrCode = () => {
            const video = videoRef.current;
            const canvas = canvasRef.current;

            if (video && video.readyState === video.HAVE_ENOUGH_DATA && canvas) {
                const context = canvas.getContext("2d");
                canvas.width = video.videoWidth;
                canvas.height = video.videoHeight;

                context.drawImage(video, 0, 0, canvas.width, canvas.height);
                const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
                const qrCode = jsQR(imageData.data, imageData.width, imageData.height);
                context.clearRect(0, 0, canvas.width, canvas.height);

                if (qrCode) {
                    store.setQrData(qrCode.data); // Update the store with the detected QR data
                } else {
                    if (store.previousQrData) {
                        store.setQrData(store.previousQrData); // Retain previous QR data if no code is found
                    } else {
                        store.setQrData(null); // Clear QR data if no previous data
                    }
                }
            }
            requestAnimationFrame(scanQrCode);
        };

        if (store.isVideoReady) {
            requestAnimationFrame(scanQrCode);
        }
    }, [store]);

    return (
        <div style={{ position: "relative", width: "100%", height: "auto" }}>
            <video
                ref={videoRef}
                playsInline
                muted
                style={{ width: "100%", height: "auto", display: store.isVideoReady ? "block" : "none" }}
            />
            {!store.isVideoReady && <p>Loading camera...</p>}

            <canvas
                ref={canvasRef}
                style={{
                    position: "absolute",
                    top: 0,
                    left: 0,
                    width: "100%",
                    height: "auto",
                    pointerEvents: "none",
                    zIndex: 2,
                }}
            />

            <div
                style={{
                    position: "absolute",
                    top: "10px",
                    left: "50%",
                    transform: "translateX(-50%)",
                    display: "flex",
                    alignItems: "center",
                    zIndex: 1,
                }}
            >
                <button
                    onClick={store.toggleCamera}
                    style={{
                        padding: "10px 20px",
                        backgroundColor: "#28a745",
                        color: "white",
                        border: "none",
                        borderRadius: "5px",
                        cursor: "pointer",
                        marginRight: "10px",
                    }}
                >
                    Switch Camera
                </button>
                {store.qrData && (
                    <div
                        style={{
                            padding: "10px",
                            backgroundColor: "#f8f9fa",
                            borderRadius: "5px",
                            boxShadow: "0px 0px 5px rgba(0,0,0,0.1)",
                            color: "#333",
                        }}
                    >
                        QR Code Data: {store.qrData}
                    </div>
                )}
            </div>
        </div>
    );
});

export default CameraButton;
