import { types } from "mobx-state-tree";

const QrStore = types
    .model("QrStore", {
        qrData: types.maybe(types.string), // Store QR data
        previousQrData: types.maybe(types.string), // Store previous QR data
        isVideoReady: types.boolean, // Track if video is ready
        facingMode: types.enumeration("FacingMode", ["environment", "user"]), // Camera mode
    })
    .actions((self) => ({
        setQrData(data) {
            self.previousQrData = self.qrData; // Store current QR data as previous
            self.qrData = data; // Update QR data
        },
        toggleCamera() {
            self.facingMode = self.facingMode === "environment" ? "user" : "environment"; // Toggle camera mode
        },
        setVideoReady(ready) {
            self.isVideoReady = ready; // Set video readiness
        },
    }));

export default QrStore;
